import { useState } from "react";
import {
  Box,
  IconButton,
  InputBase,
  Typography,
  Select,
  MenuItem,
  FormControl,
  useTheme,
  useMediaQuery,
  Modal,
  Tooltip,
  Badge,
  AppBar
} from "@mui/material";
import PersonSearchOutlinedIcon from '@mui/icons-material/PersonSearchOutlined';
import {
  Search,
  Message,
  DarkMode,
  LightMode,
  Notifications,
  Help,
  Menu,
  Close,
} from "@mui/icons-material";
import { useDispatch, useSelector } from "react-redux";
import { setMode, setLogout } from "state";
import { useNavigate } from "react-router-dom";
import FlexBetween from "components/FlexBetween";
import Friend from "components/Friend";

import HorizontalLinearStepper from "components/Helper";

import { setFilter } from "state";

const Navbar = ({ userId, picturePath }) => {
  const [isMobileMenuToggled, setIsMobileMenuToggled] = useState(false);
  const [openPopover, setOpenPopover] = useState(false);
  const [openModal, setOpenModal] = useState(false);
  const [finding, setFinding] = useState('');
  const [results, setResults] = useState([]);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const user = useSelector((state) => state.user);
  const token = useSelector((state) => state.token);
  const isNonMobileScreens = useMediaQuery("(min-width: 1000px)");
  const theme = useTheme();
  const neutralLight = theme.palette.neutral.light;
  const dark = theme.palette.neutral.dark;
  const background = theme.palette.background.default;
  const primaryLight = theme.palette.primary.light;
  const alt = theme.palette.background.alt;
  const [openHelp, setOpenHelp] = useState(false)
  const [start, setStart] = useState(true)
  const filter = useSelector((state) => state.filter)
  const fullName = `${user.firstName} ${user.lastName}`;
  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
  };
  const style2 = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 800,

    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
  };

  const getFinding = async () => {
    console.log(finding)
    const findingResponse = await fetch("http://localhost:3001/findings", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ name: finding }),
    });
    const findings = await findingResponse.json();
    console.log(findings)
    setResults(findings)
  };

  return (
    <AppBar>

      <FlexBetween padding="1rem 6%" backgroundColor={alt}>
        <FlexBetween gap="1.75rem">
          <Typography
            fontWeight="bold"
            fontSize="clamp(1rem, 2rem, 2.25rem)"
            color="primary"
            onClick={() => navigate("/home")}
            sx={{
              "&:hover": {
                color: primaryLight,
                cursor: "pointer",
              },
            }}
          >
            bujio
          </Typography>
          {isNonMobileScreens && (
            <>
              <Search />
              <FlexBetween
                backgroundColor={neutralLight}
                borderRadius="9px"
                gap="3rem"
                padding="0.1rem 1.5rem"
              >

                <InputBase placeholder="Search..." onChange={(key) => dispatch(setFilter({ filter: key.target.value }))} value={filter} />

              </FlexBetween>
              {/* {filter !=='' && <Chip label={filter} sx={{fontWeight: '550'}}></Chip>} */}
            </>
          )}
        </FlexBetween>

        {/* DESKTOP NAV */}
        {isNonMobileScreens ? (
          <FlexBetween gap="2rem">
            <Tooltip title={theme.palette.mode}>
              <IconButton onClick={() => dispatch(setMode())}>
                {theme.palette.mode === "dark" ? (
                  <DarkMode sx={{ fontSize: "25px" }} />
                ) : (
                  <LightMode sx={{ color: dark, fontSize: "25px" }} />
                )}
              </IconButton>
            </Tooltip>

            <Tooltip title='search users'>
              <IconButton onClick={() => { setOpenModal(!openModal) }}>
                {theme.palette.mode === "dark" ? (
                  <PersonSearchOutlinedIcon sx={{ fontSize: "25px" }} />
                ) : (
                  <PersonSearchOutlinedIcon sx={{ color: dark, fontSize: "25px" }} />
                )}
              </IconButton>
            </Tooltip>

            <Modal
              open={openModal}
              onClose={() => { setOpenModal(false) }}
              aria-labelledby="modal-modal-title"
              aria-describedby="modal-modal-description"
            >
              <Box sx={style} >
                <Typography id="modal-modal-title" variant="h6" component="h2">
                  <FlexBetween
                    backgroundColor={neutralLight}
                    borderRadius="9px"
                    gap="3rem"
                    padding="0.1rem 1.5rem"
                  >
                    <InputBase placeholder="Search..." onChange={(e) => { setFinding(e.target.value) }} />
                    <IconButton onClick={getFinding}>
                      <Search />
                    </IconButton>
                  </FlexBetween>
                </Typography>
                <Typography id="modal-modal-description" sx={{ mt: 2 }}>
                  {results.map((friend) => (
                    <Typography key={friend._id} sx={{ px: "0.6rem", py: "0.2rem" }}>
                      <Friend
                        key={friend._id}
                        friendId={friend._id}
                        name={`${friend.firstName} ${friend.lastName}`}
                        subtitle={friend.department}
                        userPicturePath={friend.picturePath}
                        small={true}
                      />
                    </Typography>
                  ))}
                </Typography>
              </Box>
            </Modal>

            <Tooltip title='help?'>
              <IconButton variant="outline" onClick={() => {
                setStart(false)
                setOpenHelp(true)
              }}>
                {start ?
                  <Badge color="secondary" variant="dot">
                    {theme.palette.mode === "dark" ? (
                      < Help sx={{ fontSize: "25px" }} />
                    ) : (
                      < Help sx={{ color: dark, fontSize: "25px" }} />
                    )}
                  </Badge>
                  :
                  theme.palette.mode === "dark" ? (
                    < Help sx={{ fontSize: "25px" }} />
                  ) : (
                    < Help sx={{ color: dark, fontSize: "25px" }} />
                  )
                }
              </IconButton>
            </Tooltip>
            <Modal
              open={openHelp}
              onClose={() => { setOpenHelp(false) }}
              aria-labelledby="modal-modal-title"
              aria-describedby="modal-modal-description"
            >
              <Box sx={style2} borderRadius='1rem'>
                <Typography id="modal-modal-title" >

                  <HorizontalLinearStepper userId={userId} picturePath={picturePath}></HorizontalLinearStepper>

                </Typography>
              </Box>
            </Modal>


            <FormControl variant="standard" value={fullName}>
              <Select
                value={fullName}
                sx={{
                  backgroundColor: neutralLight,
                  width: "150px",
                  borderRadius: "0.25rem",
                  p: "0.25rem 1rem",
                  "& .MuiSvgIcon-root": {
                    pr: "0.25rem",
                    width: "3rem",
                  },
                  "& .MuiSelect-select:focus": {
                    backgroundColor: neutralLight,
                  },
                }}
                input={<InputBase />}
              >
                <MenuItem value={fullName}>
                  <Typography>{fullName}</Typography>
                </MenuItem>
                <MenuItem onClick={() => dispatch(setLogout())}>Log Out</MenuItem>
              </Select>
            </FormControl>
          </FlexBetween>
        ) : (
          <IconButton
            onClick={() => setIsMobileMenuToggled(!isMobileMenuToggled)}
          >
            <Menu />
          </IconButton>
        )
        }

        {/* MOBILE NAV */}
        {
          !isNonMobileScreens && isMobileMenuToggled && (
            <Box
              position="fixed"
              right="0"
              bottom="0"
              height="100%"
              zIndex="10"
              maxWidth="500px"
              minWidth="300px"
              backgroundColor={background}
            >
              {/* CLOSE ICON */}
              <Box display="flex" justifyContent="flex-end" p="1rem">
                <IconButton
                  onClick={() => setIsMobileMenuToggled(!isMobileMenuToggled)}
                >
                  <Close />
                </IconButton>
              </Box>

              {/* MENU ITEMS */}
              <FlexBetween
                display="flex"
                flexDirection="column"
                justifyContent="center"
                alignItems="center"
                gap="3rem"
              >
                <IconButton
                  onClick={() => dispatch(setMode())}
                  sx={{ fontSize: "25px" }}
                >
                  {theme.palette.mode === "dark" ? (
                    <DarkMode sx={{ fontSize: "25px" }} />
                  ) : (
                    <LightMode sx={{ color: dark, fontSize: "25px" }} />
                  )}
                </IconButton>
                <Message sx={{ fontSize: "25px" }} />
                <Notifications sx={{ fontSize: "25px" }} />
                <Help sx={{ fontSize: "25px" }} />
                <FormControl variant="standard" value={fullName}>
                  <Select
                    value={fullName}
                    sx={{
                      backgroundColor: neutralLight,
                      width: "150px",
                      borderRadius: "0.25rem",
                      p: "0.25rem 1rem",
                      "& .MuiSvgIcon-root": {
                        pr: "0.25rem",
                        width: "3rem",
                      },
                      "& .MuiSelect-select:focus": {
                        backgroundColor: neutralLight,
                      },
                    }}
                    input={<InputBase />}
                  >
                    <MenuItem value={fullName}>
                      <Typography>{fullName}</Typography>
                    </MenuItem>
                    <MenuItem onClick={() => dispatch(setLogout())}>
                      Log Out
                    </MenuItem>
                  </Select>
                </FormControl>
              </FlexBetween>
            </Box>
          )
        }
      </FlexBetween >
    </AppBar>
  );
};

export default Navbar;
